import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { TIMESTAMP, REFRESH } from '../dates';
import CalendarDisplay from './CalendarDisplay';
import BlogExcerpt from './BlogExcerpt';

class Home extends Component {
	constructor(props) {
		super(props)
		this.state = {
			months: 1
		}
	}

	componentWillMount() {
		const query = {	per_page: 10 };
		if (TIMESTAMP - this.props.calendar.loadedAt > REFRESH ) this.props.fetchCalendar({ query });
		if (TIMESTAMP - this.props.bands.loadedAt > REFRESH ) this.props.fetchBands({ query });
		if (TIMESTAMP - this.props.venues.loadedAt > REFRESH ) this.props.fetchVenues({ query });
		if (TIMESTAMP - this.props.blog.loadedAt > REFRESH ) this.props.fetchBlog({ query });
	}

	render() {
		return (
			<section className="home">
				<h1>J. Walter Hawkes</h1>
				<h2>New York City, New York, United States</h2>
				<p>Trombone, Ukulele: Any Style, Any Volume. Four time Emmy winning composer providing compositions and arrangements for films, cartoons, etc.</p>
				<CalendarDisplay
					bands={this.props.bands}
					venues={this.props.venues}
					calendar={this.props.calendar}
					months={this.state.months}
					orientation={this.props.UI.orientation}
					isMobile={this.props.UI.isMobile}
				/>
				<p><Link to="/calendar" className="more-calendar-button"><i className="fa fa-angle-double-right fa-lg"></i><span><i>more calendar dates</i></span></Link></p>
				<SingleBlog
					data={this.props.blog.data}
					status={this.props.blog.status}
				/>
			</section>
		)
	}
}

export default Home;

function SingleBlog(props) {
	const posts = props.data.slice(0, 2);

	return (
		<section className="recent-blog-posts">
			<h2>Recent Blog Posts</h2>
			<BlogExcerpt data={posts} />
		</section>
	)
}


