import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class About extends Component {
	constructor(props) {
		super(props)
		this.state = {
			months: 1
		}
	}

	componentWillMount() {
	}

	render() {
		return (
			<section className="about">
				<h1>J. Walter Hawkes</h1>
				<h2>New York City, New York, United States</h2>
				<p>Trombone, Ukulele: Any Style, Any Volume. Four time Emmy winning composer providing compositions and arrangements for films, cartoons, etc.</p>
			</section>
		)
	}
}

export default About;


