import { fetchACF } from './request';
import { TIMESTAMP } from '../dates';
import WPActions from './wp-actions';
import generateActions from './action-creator';

const { constants, actions } = generateActions(WPActions, 'CATEGORY');

function categoryPostsHook() {

	return (dispatch, getState) => {
		const { totalPages, currentPage } = getState().categories;

		// check if done requesting
		if (currentPage > totalPages) {
			dispatch(actions.categoryPostsSuccess());
			dispatch(actions.categoryPostsTimeStamp(TIMESTAMP));
		}
	}
}

function categoryPostsHeader(Header) {

	return (dispatch, getState) => {
		const { currentPage, totalPages } = getState().categorys;

		const x_wp_totalpages = Number(Header.get('x-wp-totalpages'));

		if (x_wp_totalpages !== totalPages) dispatch(actions.categoryPostsTotalPages(x_wp_totalpages));

		// update current page before dispatching async action
		dispatch(actions.categoryPostCurrentPage(currentPage + 1));
		
		// check to see if we need to paginate	
		if (currentPage < x_wp_totalpages) {

			// dispatch next async action
			dispatch(fetchCategories({
				query: {
					per_page: 10,
					page: currentPage + 1
				}
			}));

		}
	}
}

const fetchCategories = fetchACF({
	type: 'categories',
	pre: actions.categoryPostsRequest,
	success: actions.categoryPostsResolve,
	error: actions.categoryPostsError,
	format: formatCategories,
	post: categoryPostsHook
});

// statuses => blank, loading, resolved, error

function formatCategories(arr) {

	// add parent_name to obj
	const formatted = arr.map((obj) => {
		let parent_name;

		if (obj.parent !== 0) {
			const parent_obj = arr.find((parent) => parent.id === obj.parent)
			parent_name = parent_obj.slug
		} else {
			parent_name = 'parent'
		}

		return Object.assign({}, obj, {
			parent_name 
		})

	})

	return formatted;
}

const myExports = { formatCategories, ...constants };

export default myExports;


