import React, { Component } from 'react';
import ReactMarkdown from 'react-markdown';
import { Link } from 'react-router-dom';
import { TIMESTAMP, REFRESH, ensureDateObject, writtenDateShort } from '../dates';

class BlogPost extends Component {
	constructor(props) {
		super(props)
		this.id = this.props.match.params.id;
		this.name = this.props.match.params.name;

		if (this.id && !this.name) {
			this.found = this.props.blog.data.find((item) => parseInt(item.id, 10) === parseInt(this.id, 10));
			this.state = {
				type: 'id'
			}
		} else if (this.name && !this.id) {
			this.found = this.props.blog.data.find((item) => item.slug === this.name);
			this.state = {
				type: 'name'
			}
		}
	}

	componentWillMount() {
		if (!this.found && this.id) {
			this.props.fetchBlogPost(null, this.id);
		} else if (!this.found && this.name) {
			this.props.fetchBlogPost({
				'filter[name]': this.name
			});
		}
	}

	render() {
		let data;

		if (this.id) {
			data = this.props.blog.data.find((item) => parseInt(item.id, 10) === parseInt(this.id, 10))
		} else if (this.name) {
			data = this.props.blog.data.find((item) => item.slug === this.name)
		}

		switch (this.props.blog.status) {
			default:
			case 'blank':
			case 'loading':
				return (
					<div className="loading">
						<i className="fa fa-spinner fa-pulse fa-3x fa-fw"></i>
					</div>
				)
			case 'error':
				return <p className="error">There was a problem loading blog. Please try again.</p>
			case 'resolved':
			case 'filtered':
				return <Markup data={data} />
		}

	}
}

export default BlogPost;

function Markup(props) {
	const regex = /\n.+\n/gi;

	return (
		<section className="blog-post">
			<article key={props.data.id}>
			<Link to="/blog"><h2>{props.data.raw.title}</h2></Link>
			<div><i>Posted on {ensureDateObject(props.data.date).toLocaleDateString()}</i></div>
			<ReactMarkdown source={props.data.raw.markdown.replace(regex, '\n')} />
			<p><Link to="/blog" className="blog-back-button"><i className="fa fa-angle-double-left fa-lg"></i><span>back to blog</span></Link></p>
			</article>
		</section>
	)
}


