import { connect } from 'react-redux';
import BlogComponent from '../components/Blog';
import BlogRouteComponent from '../components/BlogRoute';
import BlogPostComponent from '../components/BlogPost';
import actions from '../actions';

const mapStateToProps = (state, ownProps) => {
	return {
		UI: state.UI,
		categories: state.categories,
		blog: state.blog
	}
}

const mapDispatchToProps = (dispatch, ownProps) => {
	return {
		fetchBlog: (query = {}) => dispatch(actions.fetchBlog(query)),
		fetchBlogPost: (query = {}) => dispatch(actions.fetchBlogPost(query)),
		fetchCategories: (query = {}) => dispatch(actions.fetchCategories(query)),
	}
}

const Blog = connect(
	mapStateToProps,
	mapDispatchToProps
)(BlogComponent)

const BlogPost = connect(
	mapStateToProps,
	mapDispatchToProps
)(BlogPostComponent)

const BlogRoute = connect(
	mapStateToProps,
	mapDispatchToProps
)(BlogRouteComponent)

export {
	Blog,
	BlogPost,
	BlogRoute
};


