import blatboyLogo from '../blatboy-logo-angle.svg';
import React, { Component } from 'react';
import Nav from './Nav';
import Title from './Title';
import MobileNav from '../containers/MobileNav';

class Header extends Component {
	constructor(props) {
		super(props)
	}

	render() {
		if (this.props.UI.isMobile) {
			return (
				<header className="mobile-header-container">
					<div className="mobile-header">
						<i className="fa fa-bars fa-large hamburger" onClick={this.props.UI.menuOn ? this.props.menuOff : this.props.menuOn}></i>
						<div className="title">
							<Title text="J. Walter Hawkes" />
						</div>
					</div>
					<MobileNav pages={this.props.pages} />
				</header>
			)
		} else {
			return (
				<header className="header">
					<div className="title">
						<Title text="J. Walter Hawkes" />
						<Nav pages={this.props.pages} />
					</div>
				</header>
			)
		}
	}
}

export default Header;


