import React from 'react';
import { Link } from 'react-router-dom';

function SiteMap(props) {
	const pages = props.pages || [];
	const parentClass = props.parentClass || 'site-map';
	const childClass = props.childClass || 'site-map-item';

	const Links = pages.map((each) => {
		const to = `/${each.slug}`
		return (
			<span key={each.slug}>
				<Link className={childClass} to={to}><span>{each.label}</span></Link>
				<Divider />
			</span>
		)
	});

	return (
		<nav className={parentClass}>
			{Links}
		</nav>
	)
}

function Divider(props) {
	return <span> | </span>
}

export default SiteMap;




