import React, { Component } from 'react';
import { Link, Switch, Route } from 'react-router-dom';
import { TIMESTAMP, REFRESH, ensureDateObject, writtenDateShort } from '../dates';
import BlogExcerpt from './BlogExcerpt';

class Blog extends Component {

	componentWillMount() {

		// initial request
		if (TIMESTAMP - this.props.blog.loadedAt > REFRESH || this.props.blog.currentPage < 2) this.props.fetchBlog({ per_page: 10 });
	}

	handleScroll = () => {
		const windowHeight = "innerHeight" in window ? window.innerHeight : document.documentElement.offsetHeight;
		const body = document.body;
		const html = document.documentElement;
		const docHeight = Math.max(body.scrollHeight, body.offsetHeight, html.clientHeight,  html.scrollHeight, html.offsetHeight);
		const windowBottom = windowHeight + window.pageYOffset;
		if (windowBottom >= docHeight) {
			if (this.props.blog.currentPage < this.props.blog.totalPages) {

				this.props.fetchBlog({ 
					per_page: 10,
					page: this.props.blog.currentPage + 1
				});
				
			}
				// prevent multiple calls against same url remove event listener for 500 ms
				window.removeEventListener('scroll', this.handleScroll);
				setTimeout(() => {
					window.addEventListener('scroll', this.handleScroll)
				}, 500);

		}		
	}

	componentWillUnmount() {
		window.removeEventListener('scroll', this.handleScroll);
	}

	componentDidMount() {
		window.addEventListener('scroll', this.handleScroll);
	}	

	render() {
		return (
			<section className="blog-posts">
				<h1>Recent Blog Posts</h1>
				<BlogExcerpt data={this.props.blog.data} status={this.props.blog.status} />
			</section>
		)
	}
}

export default Blog;


