import React, { Component } from 'react';
import Spinner from './Spinner';
import CalendarMonth from './CalendarMonth';

function CalendarDisplay(props) {
	const status = cascadingStatus([props.calendar.staus, props.venues.status, props.bands.status]);

	if (props.isMobile && props.orientation === 'portrait') return <TiltScreen />
	else {
		switch (status) {
			default:
			case 'blank':
			case 'loading':
				return <Spinner />
			case 'error':
				return <p className="error">There was a problem loading calendar. Please try again.</p>
			case 'resolved':
			case 'filtered':
				return (
					<section className="current-calendar">
						<CalendarMonth
							bands={props.bands.data}
							venues={props.venues.data}
							calendar={props.calendar.data}
							months={props.months}
						/>
					</section>
				)
		}
	}
}

function TiltScreen() {
	return (
		<div>
			<p><i className="fa fa-repeat fa-2x"></i></p>
			<p>Please rotate screen to view upcoming performances</p>
		</div>
	)
}

function cascadingStatus(statuses) {
	if (statuses.includes('error')) return 'error';
	else if (statuses.includes('loading')) return 'loading';
	else if (statuses.includes('resolved')) return 'resolved';
}

export default CalendarDisplay;


