import React, { Component } from 'react';
import SiteMap from './SiteMap'

class Footer extends Component {
	constructor(props) {
		super(props)
		this.year = new Date().getFullYear();
	}

	footerStyle() {
		let style = {};

		return style;
	}

	render() {


		return (
			<footer className="footer" style={this.footerStyle()}>
				<SiteMap pages={this.props.pages} />
				<p className="social">
					<span>Find us on </span> 
					<Icon fa="facebook-square" link="http://something.com" />
					<Icon fa="twitter-square" link="https://twitter.com/blatboy" />
					<Icon fa="imdb" link="http://www.imdb.com/name/nm1499302/" />
					<Icon fa="linkedin-square" link="http://www.linkedin.com/in/blatboy" />
				</p>
				<p className="footer-text"><i className="fa fa-code"></i> Web Design by Nick Myers</p>
				<p className="footer-text"><i className="fa fa-copyright"></i> {this.year} Blatboy Productions LLC</p>
			</footer>
		)
	}
}

export default Footer;

class Icon extends Component {
	constructor(props) {
		super(props)
		this.fa = this.props.fa || 'home';
		this.link = this.props.link || '';
		this.size = this.props.size || '2x';
	}
	
	render() {
		const faIconClass = `fa fa-${this.fa} fa-${this.size}`

		return (
			<a href={this.link} target="_blank" rel="noreferrer noopener">
				<i className={faIconClass}></i>
			</a> 
		)
	}
}

