import actions from '../actions';

const contactFields = {
	name: '',
	email: '',
	subject: '',
	message: '',
	success: '',
	error: ''
}

const messages = {
	success: '',
	error: ''
}

const contactForm = Object.assign({}, contactFields, messages)

export function contactReducer(state = contactForm, action) {
	switch (action.type) {
		case actions.UPDATE_CONTACT_FORM:
			return Object.assign({}, state, {
				[action.key]: action.payload
			});
		case actions.SUBMIT_CONTACT_FORM_SUCCESS:
		case actions.SUBMIT_CONTACT_FORM_ERROR:
			return Object.assign({}, state, {
				[action.key]: action.value
			});
		case actions.SUBMIT_CONTACT_FORM:
			return Object.assign({}, state, messages);
		case actions.CLEAR_CONTACT_FORM:
			return Object.assign({}, state, contactFields);			
		default:
			return state;
	}
}


