import React, { Component } from 'react';
import { Link } from 'react-router-dom';

function Nav(props) {
	const style = {
		width: `${90 / props.pages.length}%`
	}

	const Links = props.pages.map((each) => {
		const to = `/${each.slug}`
		return <Link key={each.slug} className="menu-item" style={style} to={to}><span>{each.label}</span></Link>
	});

	return (
		<nav className="menu">
			{Links}
		</nav>
	)

}

export default Nav;


