import { connect } from 'react-redux';
import HomeComponent from '../components/Home';
import actions from '../actions';

const mapStateToProps = (state, ownProps) => {
	return {
		UI: state.UI,
		categories: state.categories,
		blog: state.blog,
		calendar: state.calendar,
		bands: state.bands,
		venues: state.venues
	}
}

const mapDispatchToProps = (dispatch, ownProps) => {
	return {
		fetchBlog: (query = {}) => dispatch(actions.fetchBlog(query)),
		fetchCalendar: (query = {}) => dispatch(actions.fetchCalendar(query)),
		fetchVenues: (query = {}) => dispatch(actions.fetchVenues(query)),
		fetchBands: (query = {}) => dispatch(actions.fetchBands(query)),
	}
}

const Home = connect(
	mapStateToProps,
	mapDispatchToProps
)(HomeComponent)

export default Home;


