import actions from '../actions';
import { initialMobileMenu } from './mobile.js';

const blankUI = {
	styles: '',
	mobileMenuStyle: initialMobileMenu,
	menuOn: false,
	isMobile: false,
	trackFilter: undefined,
	videoFilter: undefined
}

export function UIReducer(state = blankUI, action) {
	switch (action.type) {
		case actions.ANIMATE_MENU:
			return Object.assign({}, state, {
				[action.key]: {...state[action.key], ...action.payload}
			});
		case actions.HIDE_MENU:
		case actions.SHOW_MENU:
		case actions.IS_MOBILE:
		case actions.IS_NOT_MOBILE:
		case actions.IS_LANDSCAPE:
		case actions.IS_PORTRAIT:
			return Object.assign({}, state, {
				[action.key]: action.value
			});
		default:
			return state;
	}
}


