import React from 'react';
import ReactMarkdown from 'react-markdown';
import { Link } from 'react-router-dom';
import { TIMESTAMP, REFRESH, ensureDateObject, writtenDateShort } from '../dates';
import Spinner from './Spinner';

function BlogExcerpt(props) {

	const posts = props.data.map((each) => {
		const to = `/blog/name/${each.slug}`

		// make line formatting better
		const regex = /\n.+\n/gi;
		const content = each.raw.markdown.replace(regex, '\n');

		// create excerpt
		const limitLines = /.+\n.+\n/gi;
		const excerpt = content.match(limitLines);

		return (
			<article className="blog-excerpt" key={each.id}>
			<Link to={to}><h2>{each.raw.title}</h2></Link>
			<div><i>Posted on {ensureDateObject(each.date).toLocaleDateString()}</i></div>
			<ReactMarkdown source={Array.isArray(excerpt) ? excerpt[0] : null} />
			<p><Link to={to} className="continue-reading-button"><i className="fa fa-angle-double-right fa-lg"></i><span><i>continue reading</i></span></Link></p>
			</article>
		)
	});

	return (
		<section className="blog">
			<StatusSwitch status={props.status} />
			{posts}
		</section>
	)
}

function StatusSwitch(props) {
	switch (props.status) {
		case 'blank':
		case 'loading':
			return <Spinner />
		case'error':
			return <p className="error">There was a problem loading blog. Please try again.</p>
		case 'filtered':
		case 'resolved':
		default:
			return null;
	}

}

export default BlogExcerpt;


