import React, { Component } from 'react';
import { Link, Switch, Route } from 'react-router-dom';
import { TIMESTAMP, REFRESH, ensureDateObject, writtenDateShort } from '../dates';
import { Blog, BlogPost } from '../containers/Blog';

class BlogRoutes extends Component {
	render() {
		return (
			<Switch>
				<Route exact path="/blog" component={Blog}></Route>
				<Route path ="/blog/id/:id" component={BlogPost}></Route>
				<Route path ="/blog/name/:name" component={BlogPost}></Route>
			</Switch>
		)
	}
}

export default BlogRoutes;


