import React from 'react';
import { TIMESTAMP, REFRESH, ensureDateObject, monthAndYearString, todayMonthString, todayYear, months } from '../dates';
import CalendarMarkup from './CalendarMarkup';
import { Link } from 'react-router-dom';

function CalendarMonth(props) {
	const calendar = [];

	for (let i = 0; i < props.months; i ++) {
		const month = wrapMonth(TIMESTAMP.getMonth(), i);
		const year = wrapYear(TIMESTAMP.getFullYear(), i);
		const data = props.calendar
			.filter((gig) => month === ensureDateObject(gig.acf.date).getMonth() && year === ensureDateObject(gig.acf.date).getFullYear())
			.sort((a, b) => ensureDateObject(a.acf.date) - ensureDateObject(b.acf.date))

		calendar.push({	month, year, data });
	}

	const monthlyGigs = calendar.map((obj) => {

		const dateString = `${capitalize(months.en[obj.month])}, ${obj.year}`;

		if (obj.data.length > 0) {

			return (
				<article key={dateString}>
				<h2>Calendar for {dateString}</h2>
				<CalendarMarkup calendar={obj.data} bands={props.bands} venues={props.venues} />
				</article>
			)
		} else return null;

	});

	return (
		<section className="current-calendar">
			{monthlyGigs}
		</section>
	)
}

export default CalendarMonth;

function capitalize(string) {
	return string.toUpperCase().charAt(0) + string.substring(1);
}

function wrapMonth(month, i) {
	if ((i % 12) === 0) return month
	return ((month - i) % 12 + 12);
}

function wrapYear(year, i) {
	return year - Math.floor(i / 12);
}


