import { connect } from 'react-redux';
import CalendarComponent from '../components/Calendar';
import actions from '../actions';

const mapStateToProps = (state, ownProps) => {
	return {
		UI: state.UI,
		calendar: state.calendar,
		bands: state.bands,
		venues: state.venues
	}
}

const mapDispatchToProps = (dispatch, ownProps) => {
	return {
		fetchCalendar: (query = {}) => dispatch(actions.fetchCalendar(query)),
		fetchVenues: (query = {}) => dispatch(actions.fetchVenues(query)),
		fetchBands: (query = {}) => dispatch(actions.fetchBands(query))
	}
}

const Calendar = connect(
	mapStateToProps,
	mapDispatchToProps
)(CalendarComponent)

export default Calendar;


