import React, { Component } from 'react';
import { Route, Switch } from 'react-router-dom';
import { pages } from './Root';

class Main extends Component {

	render() {
		return (
			<div className="content">
				<MyRoutes pages={pages} />
			</div>
		)
	}
}

export default Main;

function MyRoutes(props) {
	const routes = props.pages.map((each) => {
		const path = `/${each.slug}`
		if (each.exact)	return <Route key={each.slug} exact path={path} component={each.component} />
		else return <Route key={each.slug} path={path} component={each.component} />
	});

	return (
		<main className="main">
			<Switch>
				{routes}
			</Switch>
		</main>
	)
}


