import { fetchACF } from './request';
import { TIMESTAMP } from '../dates';
import WPActions from './wp-actions';
import generateActions from './action-creator';

const { constants, actions } = generateActions(WPActions, 'BLOG');

function blogPostsHook() {

	return (dispatch, getState) => {
		// const { totalPages, currentPage } = getState().blog;

		// check if done requesting
		// if (currentPage > totalPages) {
			dispatch(actions.blogPostsSuccess());
			dispatch(actions.blogPostsTimestamp(TIMESTAMP));
		// }
	}
}

function blogPostsHeader(Header) {

	return (dispatch, getState) => {
		const { currentPage, totalPages } = getState().blog;

		const x_wp_totalpages = Number(Header.get('x-wp-totalpages'));

		if (x_wp_totalpages !== totalPages) dispatch(actions.blogPostsTotalPages(x_wp_totalpages));

		// update current page before dispatching async action
		dispatch(actions.blogPostsCurrentPage(currentPage + 1));

		// check to see if we need to paginate
		if (currentPage < x_wp_totalpages) {

			// dispatch next async action
			// dispatch(fetchBlog({
			// 	per_page: 10,
			// 	page: currentPage + 1
			// }));

		}
	}
}

const fetchBlog = fetchACF({
	type: 'posts',
	header: blogPostsHeader,
	pre: actions.blogPostsRequest,
	success: actions.blogPostsResolve,
	error: actions.blogPostsError,
	post: blogPostsHook
});

const fetchBlogPost = fetchACF({
	type: 'posts',
	pre: actions.blogPostsRequest,
	success: actions.blogPostsResolve,
	error: actions.blogPostsError,
	post: blogPostsHook
})

const myExports = { ...constants, fetchBlog, fetchBlogPost }

export default myExports

// statuses => blank, loading, resolved, error


