import React, { Component } from 'react';
import { TIMESTAMP, REFRESH } from '../dates';
import CalendarDisplay from './CalendarDisplay';

class Calendar extends Component {
	constructor(props) {
		super(props)
		this.state = {
			months: 4
		};
	}

	componentWillMount() {
		const query = {	per_page: 10 };
		if (TIMESTAMP - this.props.calendar.loadedAt > REFRESH ) this.props.fetchCalendar({ query });
		if (TIMESTAMP - this.props.bands.loadedAt > REFRESH ) this.props.fetchBands({ query });
		if (TIMESTAMP - this.props.venues.loadedAt > REFRESH ) this.props.fetchVenues({ query });
	}

	handleScroll = () => {
		const windowHeight = "innerHeight" in window ? window.innerHeight : document.documentElement.offsetHeight;
		const body = document.body;
		const html = document.documentElement;
		const docHeight = Math.max(body.scrollHeight, body.offsetHeight, html.clientHeight,  html.scrollHeight, html.offsetHeight);
		const windowBottom = windowHeight + window.pageYOffset;
		if (windowBottom >= docHeight) {

			this.setState((prevState, props) => {
				return {
					months: prevState.months + 4
				}
			});

			// prevent multiple calls against same url remove event listener for 500 ms
			window.removeEventListener('scroll', this.handleScroll);
			setTimeout(() => {
				window.addEventListener('scroll', this.handleScroll)
			}, 500);

		}		
	}

	componentWillUnmount() {
		window.removeEventListener('scroll', this.handleScroll);
	}

	componentDidMount() {
		window.addEventListener('scroll', this.handleScroll);
	}	

	render() {
		return (
			<section className="calendar">
				<CalendarDisplay
					bands={this.props.bands}
					venues={this.props.venues}
					calendar={this.props.calendar}
					months={this.state.months}
					orientation={this.props.UI.orientation}
					isMobile={this.props.UI.isMobile}
				/>
			</section>
		)
	}
}

export default Calendar;


